.profile {
        width: 250px;
        text-align: center;
        margin: auto;
        border: solid 1px #2e5e99;
 
}

.description {
	width: 250px;
	height: auto;
}

.avatar {
	width: 128px;
    height: 128px;
	border-radius: 50%;
	text-align: center;
    margin-top: 30px;
	
}
.name {
	font-size: 18px;
	color: #333333;
	font-weight: 700;
	margin-top: 30px;
}

.tag {
	font-size: 14px;
	color: #666666;
	text-align: center;
	margin-top: 10px;
}

.location {
	font-size: 14px;
	color: #97a2ae;
	line-height: 32px;
	margin-top: 10px;
	margin-bottom: 20px;
}

.stats {
    display: flex;
    flex-wrap: nowrap;
    list-style: none;
    margin: 0;
    padding: 0;
  }

.stats > li {
    flex-basis: 33.33%;
    display: flex;
    flex-wrap: wrap;
    height: 73px;
    border: 1px solid #e4e9f0;
    background-color: #f3f6f9;
  }


.label,
.quantity {
    flex-basis: 100%;
    text-align: center;
    font-family: "Lato", sans-serif;
    line-height: 20px;
  }
.label {
    font-weight: 400;
    font-size: 12px;
    color: #768696;
    margin-top: auto;
  }
  
.quantity {
    font-weight: 900;
    font-size: 14px;
    color: #1f3349;
    margin-bottom: auto;
  }