.transaction_history {
    margin: 30px auto;
    width: 872px;
    border-radius: 3px;
    background-color: rgb(255, 255, 255);
    box-shadow: 0px 2px 3px 0px rgba(0, 0, 0, 0.25);
    text-align: center;
    table-layout: fixed;
    border-collapse: collapse;
    /* border-spacing: 1px;
    border-color: #000; */
    font-family: "Helvetica", "Roboto", sans-serif;
  }
  
  .head {
    color: #fff;
    text-transform: uppercase;
    font-size: 13px;
    font-weight: 800;
    line-height: 30px;
    background-color: #00bcd4;
  }
  
  .body {
    font-size: 15px;
    font-weight: 300;
    line-height: 30px;
  }
  
 